        name: 'Components',
        components: {
            Footer,
            Button,
            Form,
            Catalogue,
            ComponentsIntro,
            Picture,
            Links,
            Tables,
            Tags,
            Tooltip,
            Alert,
            Accordion,
            Application,
            Input,
