            Dropdown,
            Breadcrumb,
            Collapse,
            Tabs,
            Create
        },
        data() {
            return {
                component: 'ComponentsIntro',
            }
        },

        methods: {
            test: function () {
                let active = document.querySelectorAll('li');
                for (let activeTab of active) {
                    if (this.component == activeTab.innerText) {
                        activeTab.classList.add('active')
                    }
                }
            }
        }

